body {
  text-align: center;
  padding: 5%;
  background-color: #333333;
  color: white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.meme-button {
  cursor: pointer;
  font-style: italic;
  padding: 10px;
}

.meme-item {
  text-align: left;
  display: inline-block;
  padding: 10px;
  position: relative;
  width: 150px;
  height: 150px;
  margin: 5px;
  cursor: pointer;
}

.meme-img {
  width: 150px;
  height: 150px;
  border-radius: 8px;
  border: 3px solid white;
  position: absolute;
  -o-object-fit: cover;
     object-fit: cover;
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

.my-meme-img {
  position: relative;
  display: inline-block;
  margin: 5px;
  width: 220px;
  border-radius: 4px;
  border: 3px solid white;
}

.meme-txt {
  width: 150px;
  text-align: center;
  position: absolute;
  border-radius: 5px;
  bottom: 0;
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  font-size: 14px;
}

.no-txt {
  display: none;
}

.darken-img {
  background-color: grey;
  -webkit-filter: brightness(10%);
          filter: brightness(10%);
  -webkit-animation-name: darken;
          animation-name: darken;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}

@-webkit-keyframes darken {
  from { -webkit-filter: brightness(90%); filter: brightness(90%); }
  to { -webkit-filter: brightness(10%); filter: brightness(10%); }
}

@keyframes darken {
  from { -webkit-filter: brightness(90%); filter: brightness(90%); }
  to { -webkit-filter: brightness(10%); filter: brightness(10%); }
}

@-webkit-keyframes fade-in {
  from { opacity: 0; }
  TO { opacity: 1; }
}

@keyframes fade-in {
  from { opacity: 0; }
  TO { opacity: 1; }
}


